import React, { useEffect, useState } from 'react';
import { WediagButton, ProgressLoadingBar } from '@ohif/ui'
import CornerstoneViewportImplantForm from '../../../cornerstone/src/utils/CornerstoneViewportImplantForm';
import { StackViewport } from '@cornerstonejs/core';
import { Types as cstTypes } from '@cornerstonejs/tools';

export default function PanoramicGenerationPanel({ commandsManager, servicesManager }): React.FunctionComponent {
  const { cornerstoneViewportService, panoramicService, viewportGridService } = servicesManager.services;

  const [statusDisplayed, setStatusDisplayed] = useState(panoramicService.isDisplayed());
  // Progress bar for panoramic computation
  const [statusEditing, setStatusEditing] = useState(panoramicService.isEditing());

  const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
  const panoramicState = panoramicService.getPanoramicState(displaySetUID);
  const [isAxesDisabled, setIsAxesDisabled] = useState(panoramicState?.teethAxes === undefined);
  const [processingPercent, setProcessingPercent] = useState(0);

  useEffect(() => {
    const events = [
      {
        event: panoramicService.EVENTS.PANORAMIC_PROCESSING,
        callback: ({ percent }) => setProcessingPercent(percent)
      },
      {
        event: panoramicService.EVENTS.STATUS_DISPLAYED_CHANGED,
        callback: ({ displayed }) => setStatusDisplayed(displayed)
      },
      {
        event: panoramicService.EVENTS.STATUS_EDITING_CHANGED,
        callback: ({ editing }) => setStatusEditing(editing)
      },
      {
        event: panoramicService.EVENTS.PANORAMIC_STATE_UPDATED,
        callback: ({ displaySetInstanceUID }) => {
          const panoramicState = panoramicService.getPanoramicState(displaySetInstanceUID);
          setIsAxesDisabled(panoramicState?.teethAxes === undefined);
        }
      }
    ];

    const subscriptions = events.map(
      ({ event, callback }) => panoramicService.subscribe(event, callback).unsubscribe
    );

    return () => {
      for (const unsub of subscriptions) {
        unsub();
      }
    }
  }, []);

  const deletePanoramicDiseases = (panoramicViewport: StackViewport) => {
    const { measurementService, toolGroupService } = servicesManager.services;

    //============================= DELETE ALL PREVIOUS DENTASCAN ANNOTATIONS =================//
    const toolGroup = toolGroupService.getToolGroupForViewport(panoramicViewport.id) as cstTypes.IToolGroup;
    const arrowAnnotateInstance = toolGroup.getToolInstance('Disease');

    const dentascanMeasurements = measurementService.getMeasurements().filter(
      m => {return m.toolName === 'Disease'}
    );
    for (const dm of dentascanMeasurements) {
      if (dm.metadata.dentascan) {
        commandsManager.runCommand('deleteMeasurement', { uid: dm.uid });
        arrowAnnotateInstance.removeDiseaseAnnotation(dm.uid);
      }
    }
  }

  const toggleAnnotation = () => {
    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const panoramicState = panoramicService.getPanoramicState(displaySetUID);
    // controlPoints are at index 0
    panoramicService.setAnnotationVisibility(displaySetUID, [panoramicState.annotationUIDs[0]], !statusEditing);
    panoramicService.setAnnotationVisibility(displaySetUID, panoramicState.annotationUIDs.slice(1), statusEditing);
    // Validating control points coordinates
    if (statusEditing) {
      // Delete diseases
      const panoramicViewportId = cornerstoneViewportService.getViewportIds()[1];
      const panoramicViewport = cornerstoneViewportService.getCornerstoneViewport(panoramicViewportId) as StackViewport;
      deletePanoramicDiseases(panoramicViewport);
      panoramicService.setPanoramicState(displaySetUID, {...panoramicState, diseases: []})
      panoramicService.panoramicPipeline(displaySetUID);
    }
    panoramicService.setEditing(!statusEditing);
  }

  const cancelAnnotation = () => {
    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const panoramicState = panoramicService.getPanoramicState(displaySetUID);
    panoramicService.setAnnotationVisibility(displaySetUID, [panoramicState.annotationUIDs[0]], false);
    panoramicService.setAnnotationVisibility(displaySetUID, panoramicState.annotationUIDs.slice(1), true);
    panoramicService.setEditing(false);
  }

  const toggleHangingProtocol = () => {
    commandsManager.runCommand('toggleHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 1 });
  }

  const displayModal = () => {
    const { uiModalService } = servicesManager.services;

    if (uiModalService) {
      uiModalService.show({
        content: CornerstoneViewportImplantForm,
        contentProps: {
          onClose: uiModalService.hide,
          servicesManager,
        },
        contentDimensions: 'w-[100%] h-[100%] !bg-grayDarker !bg-opacity-80 rounded-lg shadow-lg',
        containerDimensions: 'w-[50%] h-[50%]',
      });
    }
    if (!panoramicService.isDisplayed()) {
      commandsManager.runCommand('panoramicView', { reset: false });
    }
  };

  // @ts-ignore
  return (
    <div className='w-full h-full flex flex-col space-y-2 items-center'>
      <p className='w-full text-default font-bold text-left'>{'Panoramic Generation'}</p>
      { statusDisplayed &&
        <WediagButton buttonCategory='sm' variant='bordered' onPress={toggleAnnotation} className='w-[90%]'>
          {!statusEditing ? 'Move control points' : 'Validate'}
        </WediagButton>
      }
      { statusDisplayed && statusEditing &&
        <WediagButton buttonCategory='sm' variant='bordered' onPress={cancelAnnotation} className='w-[90%]'>
          {'Cancel'}
        </WediagButton>
      }
      <WediagButton buttonCategory='sm' variant='bordered' onPress={toggleHangingProtocol} className='w-[90%]'>
        {'Dentascan Focus'}
      </WediagButton>

      <p className='w-full text-default font-bold text-left'>{'Implantology'}</p>

      <WediagButton buttonCategory='sm' variant='bordered' onPress={() => {}} className='w-[90%]'>
        {'Choose implant model'}
      </WediagButton>

      <WediagButton buttonCategory='sm' variant='bordered' onPress={() => displayModal()} className='w-[90%]' isDisabled={isAxesDisabled}>
        {'Add implant'}
      </WediagButton>

      { processingPercent !== 0 &&
        <div className={'w-full mt-[10px] mb-[20px]'}>
          <p className='w-full text-default text-left'>{'Creating panoramic...'}</p>
          <ProgressLoadingBar progress={processingPercent}/>
        </div>
      }
    </div>
  );
}
